.security {
  &-title {
    margin-bottom: rem(30);
    border-bottom: 1px solid var(--color-gray-200);
    padding-bottom: rem(30);
  }

  &-row {
    margin-bottom: rem(30);

    &:not(:last-child) {
      margin-bottom: rem(50);
    }
  }

  &-tooltip {
    margin-left: 5px;
    width: 11px;
    height: 11px;
  }

  &-item {
    @include flex-e-sb;

    &:not(:last-child) {
      margin-bottom: rem(40);
    }

    &-box {
      margin-right: 20px;
      width: 100%;
      max-width: 475px;

      @include for-width(-mobile-sl) {
        margin-right: 0;
        margin-bottom: 20px;
        width: 100%;
      }
    }

    .api-tooltip::before {
      max-width: 250px;
      transform: translate(20px, -50%);
    }

    @include for-width(-mobile-sl) {
      flex-wrap: wrap;
    }
  }

  &-name {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    font-size: var(--size-s);
    line-height: 1.5;
    letter-spacing: 0.015em;
    text-transform: capitalize;
    color: var(--color-gray-400);
  }

  &-content.input {
    margin: 0;
    padding: 12px rem(20);
    cursor: default;

    &::placeholder {
      font-size: var(--size-m);
    }
  }

  &-btn {
    width: 100%;
    height: 33px;
    max-width: 130px;
    transform: translateY(-3px);

    @include for-width(-mobile-sl) {
      max-width: 100%;
      transform: translateY(0);
    }
  }

  &-passwords {
    padding: rem(30) 30px;
  }

  &-password {
    &-btn {
      position: absolute;
      right: 0;
      padding: 0 rem(15);
      height: 100%;
    }

    &:not(:last-child) {
      margin-bottom: rem(30);
    }

    &:last-child {
      margin-bottom: rem(10);
    }
  }

  &-change {
    position: relative;
  }

  &-icon {
    width: 12px;
    height: 9px;
    fill: currentColor;
  }

  &-box {
    &.inner {
      padding-top: clamp(0.5em, rem(30), 30px);
    }
  }
}
